@font-face {
  font-family: "Flaticon";
  src: url("./Flaticon/Flaticon.eot");
  src: url("./Flaticon/Flaticon.eot?#iefix") format("embedded-opentype"),
       url("./Flaticon/Flaticon.woff2") format("woff2"),
       url("./Flaticon/Flaticon.woff") format("woff"),
       url("./Flaticon/Flaticon.ttf") format("truetype"),
       url("./Flaticon/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url("./Flaticon/Flaticon.svg#Flaticon") format("svg");
  }
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
  font-family: Flaticon;
  font-style: normal;
}

.xsmall:before, .xsmall:after  {
  margin: 0.5rem;
  font-size: 1.5rem;
}
.small:before, .small:after  {
  margin: 0.75rem;
  font-size: 2rem;
}
.medium:before, .medium:after  {
  margin: 1rem;
  font-size: 2.5rem;
}
.large:before, .large:after  {
  margin: 1.25rem;
  font-size: 3rem;
}
.xlarge:before, .xlarge:after  {
  margin: 1.5rem;
  font-size: 3.5rem;
}
.jumbo:before, .jumbo:after  {
  margin: 1rem;
  font-size: 7.5rem;
}

